.input, .textarea {
    background-color: #fff;
    width: 100%;
    border: 1px solid black;
    padding: 7px 20px;
    margin: 10px 0px;
    resize: none;
    font-family: var(--main-font);
}

.input:focus {
    outline: none;
}

.input ::placeholder {
    color: #999;
    font-family: var(--main-font);
    font-size: 16px;
}

.message {
    height: 300px;
}
